<script>
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

// 修复Leaflet默认图标问题
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

export default {
  name: 'MapPage',
  data() {
    return {
      map: null,
      demoInterval: null,
      isDemoRunning: false,
      currentAlgorithm: 'Q-learning',
      showDemoModal: false,
      demoProgress: 0,
      demoStats: {
        pathLength: 0,
        coverage: 0,
        efficiency: 0
      },
      pathLayer: null
    };
  },
  mounted() {
    // 页面加载时滚动到顶部
    this.scrollToTop()
    this.initMap();
  },
  beforeUnmount() {
    this.resetDemo();
    if (this.map) {
      this.map.remove();
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo({
        top: 0
      })
    },
    initMap() {
      // 初始化地图
      this.map = L.map(this.$refs.mapContainer).setView([31.2304, 121.4737], 13);
      
      // 添加高德卫星地图图层 - 移除密钥参数，添加错误处理
      const gaodeSatellite = L.tileLayer('https://webst0{s}.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}', {
        subdomains: ['1', '2', '3', '4'],
        attribution: '高德地图 2025 · 空间智能',
        maxZoom: 18,
        crossOrigin: true,
        errorTileUrl: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mNk+M9QDwADhgGAWjR9awAAAABJRU5ErkJggg=='
      });
      
      // 可选：添加高德路网图层作为叠加
      const gaodeRoad = L.tileLayer('https://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}', {
        subdomains: ['1', '2', '3', '4'],
        attribution: '高德地图 2025 · 空间智能',
        maxZoom: 18,
        crossOrigin: true,
        errorTileUrl: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mNk+M9QDwADhgGAWjR9awAAAABJRU5ErkJggg=='
      });
      
      // 移除OSM备选图层
      
      // 创建图层组
      const satelliteWithRoads = L.layerGroup([gaodeSatellite, gaodeRoad]);
      
      // 添加图层控制，允许用户切换显示模式
      const baseLayers = {
        "卫星影像": gaodeSatellite,
        "卫星影像+路网": satelliteWithRoads
      };
      
      L.control.layers(baseLayers).addTo(this.map);
      
      // 默认显示纯卫星影像，添加错误处理
      try {
          gaodeSatellite.addTo(this.map);
          // 移除备选图层切换逻辑
          gaodeSatellite.on('tileerror', () => {
            console.warn('卫星影像加载失败');
          });
        } catch (e) {
          console.error('加载卫星影像失败:', e);
        }
      
      // 添加示例农田区域
      const fieldBounds = [
        [31.228, 121.468],
        [31.228, 121.478],
        [31.235, 121.478],
        [31.235, 121.468]
      ];
      
      L.polygon(fieldBounds, {
        color: '#4caf50',
        fillColor: '#4caf50',
        fillOpacity: 0.3,
        weight: 3
      }).addTo(this.map).bindPopup('示例农田区域');
      
      // 添加障碍物
      const obstacles = [
        {lat: 31.230, lng: 121.471, radius: 100},
        {lat: 31.233, lng: 121.475, radius: 80}
      ];
      
      obstacles.forEach(obstacle => {
        L.circle([obstacle.lat, obstacle.lng], {
          color: '#ff6b6b',
          fillColor: '#ff6b6b',
          fillOpacity: 0.7,
          radius: obstacle.radius,
          weight: 2
        }).addTo(this.map).bindPopup('障碍物区域');
      });
      
      // 添加图例
      this.addLegend();
    },
    
    addLegend() {
      const legend = L.control({ position: 'bottomright' });
      
      legend.onAdd = () => {
        const div = L.DomUtil.create('div', 'map-legend');
        div.innerHTML = `
          <h4>图例</h4>
          <div class="legend-item">
            <div class="legend-color" style="background-color: #4caf50;"></div>
            <span>农田区域</span>
          </div>
          <div class="legend-item">
            <div class="legend-color" style="background-color: #ff6b6b;"></div>
            <span>障碍物</span>
          </div>
          <div class="legend-item">
            <div class="legend-color" style="background-color: #2c5aa0; border: 2px solid #2c5aa0;"></div>
            <span>规划路径</span>
          </div>
        `;
        return div;
      };
      
      legend.addTo(this.map);
    },
    
    startExperience() {
      console.log('开始智能喷洒规划体验');
      // 这里可以跳转到实际的操作页面或打开配置面板
    },
    
    showDemo() {
      this.showDemoModal = true;
      this.startDemo();
    },
    
    closeDemoModal() {
      this.showDemoModal = false;
      this.resetDemo();
    },
    
    startDemo() {
      if (this.isDemoRunning) return;
      
      this.resetDemo();
      this.isDemoRunning = true;
      this.demoProgress = 0;
      
      // 初始化演示统计
      this.demoStats = {
        pathLength: 0,
        coverage: 0,
        efficiency: 0
      };
      
      // 生成路径点模拟算法过程
      const pathPoints = this.generatePathPoints();
      this.pathLayer = L.polyline([], {
        color: '#2c5aa0',
        weight: 4,
        opacity: 0.8,
        dashArray: '5, 5'
      }).addTo(this.map);
      
      let currentIndex = 0;
      const totalPoints = pathPoints.length;
      
      this.demoInterval = setInterval(() => {
        if (currentIndex < totalPoints) {
          this.pathLayer.addLatLng(pathPoints[currentIndex]);
          currentIndex++;
          
          // 更新进度和统计
          this.demoProgress = Math.round((currentIndex / totalPoints) * 100);
          this.demoStats.pathLength = Math.round(currentIndex * 25);
          this.demoStats.coverage = Math.min(98, Math.round((currentIndex / totalPoints) * 100));
          this.demoStats.efficiency = Math.min(95, Math.round((currentIndex / totalPoints) * 100));
        } else {
          this.isDemoRunning = false;
          clearInterval(this.demoInterval);
          console.log('路径规划演示完成！');
        }
      }, 100);
    },
    
    generatePathPoints() {
      const latMin = 31.228;
      const latMax = 31.235;
      const lngMin = 121.468;
      const lngMax = 121.478;
      
      const pathPoints = [];
      
      // 生成系统性的路径点，模拟智能路径规划
      for (let lat = latMin; lat <= latMax; lat += 0.0005) {
        for (let lng = lngMin; lng <= lngMax; lng += 0.001) {
          // 模拟避障逻辑
          const isInObstacle = this.isPointInObstacle(lat, lng);
          if (!isInObstacle) {
            pathPoints.push([lat, lng]);
          }
        }
      }
      
      return pathPoints;
    },
    
    isPointInObstacle(lat, lng) {
      // 简单模拟障碍物检测
      const obstacles = [
        {lat: 31.230, lng: 121.471, radius: 0.0005},
        {lat: 31.233, lng: 121.475, radius: 0.0004}
      ];
      
      return obstacles.some(obstacle => {
        const distance = Math.sqrt(
          Math.pow(lat - obstacle.lat, 2) + Math.pow(lng - obstacle.lng, 2)
        );
        return distance < obstacle.radius;
      });
    },
    
    resetDemo() {
      this.isDemoRunning = false;
      if (this.demoInterval) {
        clearInterval(this.demoInterval);
        this.demoInterval = null;
      }
      
      if (this.pathLayer) {
        this.map.removeLayer(this.pathLayer);
        this.pathLayer = null;
      }
      
      this.demoProgress = 0;
    },
    
    toggleAlgorithm() {
      this.currentAlgorithm = this.currentAlgorithm === 'Q-learning' ? 'Deep Q-Network' : 'Q-learning';
      console.log(`已切换到${this.currentAlgorithm}算法`);
      this.resetDemo();
    }
  }
};
</script>

